pre,
code,
sub,
sup,
fieldset,
form,
label,
legend,
details,
embed,
menu,
summary,
table,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul,
caption,
dl,
dt,
dd,
li {
  margin: 0;
  padding: 0;
  font: inherit;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

small {
  font-size: 16px;
}

sub,
sup {
  font-size: 16px;
}

sup {
  top: -.5rem;
}

sub {
  bottom: -0.25rem;
}

code,
kbd,
pre,
samp {
  font-size: 16px;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

optgroup {
  font-weight: 600;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  width: auto;
  height: auto;
  text-align: center;
}

h2 {
  font-family: "raleway",Helvetica,sans-serif;
  font-size: 44px;
  color: #666;
  display: inline-block;
}

p.meta {
  font-family: "aktiv-grotesk-std",Helvetica,sans-serif;
  font-size: 14px;
  color: #666;
  margin: 5px;
}

a {
  color: #337ab7;
  text-decoration: none;
}

.chart-container {
  display: inline-block;
  max-width: 980px;
  width: 100%;
  margin: 15px auto;
  text-align: left;
}

@media (max-width: 445px) {
  .chart-container {
    text-align: center;
  }
}

.items {
  width: 100%;
  display: inline-block;
  text-align: center;
  margin: 0 -15px;
}

@media (max-width: 800px) {
  .items {
    text-align: left;
  }
}

@media (max-width: 445px) {
  .items {
    text-align: center;
  }
}

.items .item {
  width: calc(25% - 2px);
  min-width: 200px;
  display: inline-block;
  text-align: center;
  padding: 15px;
  margin-top: 15px;
}

.items .item:hover {
  cursor: pointer;
}

.items .item:hover img {
  outline: 1px solid #ccc;
}

.items .item:hover p {
  color: #4a4a4a;
}

@media (max-width: 800px) {
  .items .item {
    width: calc(33% - 2px);
  }
}

@media (max-width: 600px) {
  .items .item {
    width: calc(50% - 2px);
  }
}

@media (max-width: 445px) {
  .items .item {
    width: calc(100% - 2px);
    max-width: 300px;
  }
}

.items .item img {
  width: 100%;
}

.items .item p {
  font-family: "aktiv-grotesk-std",Helvetica,sans-serif;
  font-size: 14px;
  color: #ccc;
  margin-top: 6px;
}


